.App {
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: #14171a;
}

body {
  margin: 0px;
  padding-bottom: 0px;
}

.links {
  display: flex;
  justify-content: space-evenly;
}

.characters, .films ,.planets,.StarShips,.vehicles,.species{
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: space-evenly;
}

.Preview{
  display: flex;
  text-align: center;
  flex-direction: column;
  margin: 10px;
}

.Preview img {
  max-width: 300px;
  max-height: 600px;
}

.Wrapper {
  background-color: #14171a;
  color: #BF9663;
}

.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.info>img {
  max-width: 80vw;
  max-height: 400px;
}

.data {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pageList {
  display: flex;
  flex-wrap: wrap;
  max-width: 600px;
}

.pageList>.filmsPreview {
  display: flex;
  flex-direction: column;
}

.pageList .filmsPreview>img {
  max-width: 100px;
  max-height: 100px;
}

.openingCrawl {
  display: flex;
  flex-wrap: wrap;
  max-width: 500px;
}

.filmMini>img {
  max-width: 100px;
  max-height: 100px;
}

.pageButtons {
  display: flex;
  justify-content: center;
}

footer {
  margin: 0px;
  padding-bottom: 0px;
  text-align: center;
  background-color: #0c0e0f;
}

footer p {
  color: #BF9663;
  margin: 0px;
}

.minis{
  display: flex;
  flex-wrap: wrap;
}
.mini{
  display: flex;
  flex-direction: column;
}

.mini>img {
  max-width: 150px;
  max-height: 200px;
}

.related{
  margin-top: 20px;
  margin-left: 20px;
}

.related .minis{
  padding-bottom: 20px;
}

.Preview a{
  color: #BF9663;
}

.Preview a:hover{
  color: #BF9663;
}

.pageButtons > button:hover,.goBack:hover{
  border-radius: 10px;
  border-color: #BF9663;
  color: #BF9663;
  background-color: #0c0e0f;
  transition: all 0.5s ease;
}

.pageButtons > button,.goBack{
  border-radius: 10px;
  border-color: #0c0e0f;
  color: #0c0e0f;
  background-color: #BF9663;
  transition: all 0.5s ease;
}

@media (max-width: 600px) {
  .related {
    margin-left: 0px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
